import { useState, useEffect } from "react"
import { inject, observer } from 'mobx-react';
import Claim from "./Claim"
import Dropdown from "../../components/Dropdown/Dropdown"
import Toggle from "../../components/Toggle/Toggle"
import Checkbox from "../../components/Checkbox/Checkbox"
import BSC from '../../assets/img/BNB.svg'
import POLYGON from '../../assets/img/polygon-logo.svg'
import ARBITRUM from '../../assets/img/arbitrum-logo.svg'
import ETH from '../../assets/img/ethereum.svg'
import Card from "./Card"
import Filter from "./Filter"
import apiProxy from '../../model/ApiProxy'
import selectIcon from '../../assets/img/arrow-down.svg'
import filterIcon from './img/filter.svg'
import './pool.less'
import Icon from "../../components/Icon/Icon";
import PoolGuide from "../../components/PoolGuide/PoolGuide";
import { getCookie } from "../../utils/utils";
import AnnouncementLp from "../../components/Announcement/AnnouncementLp"
function Index({ lang, getLang, wallet = {}, poolManager }) {
  const [curTabPool, setCurTabPool] = useState('amm');
  const [curOpen, setCurOpen] = useState(false);
  const [poolTitle, setPoolTitle] = useState(lang['deri-pool-title'])
  const [nowPool, setNowPool] = useState([]);
  const [checkedOption, setCheckedOption] = useState(false)
  const [isShowFilter, setIsShowFilter] = useState(false)
  const [checkedFutureMain, setCheckedFutureMain] = useState(false)
  const [checkedFutureInno, setCheckedFutureInno] = useState(false)
  const [checkedBSC, setCheckedBSC] = useState(false)
  const [checkedArb, setCheckedArb] = useState(false)
  const [checkedPolygon, setCheckedPolygon] = useState(false)
  const [checkedETH, setCheckedETH] = useState(false)
  const [checkedStaked, setCheckedStaked] = useState(false)
  const [stakedAmmList, setStakedAmmList] = useState([])
  const [stakedLpList, setStakedLpList] = useState([])
  const [poolClaim, setPoolClaim] = useState(false)
  const dropdownList = [
    {
      text: lang['deri-pool-title'],
      index: 'official'
    },
    {
      text: lang['open-pool-title'],
      index: 'open'
    }
  ]
  const selectPoolOnchage = (index) => {
    if (index === 'official') {
      setNowPool(poolManager.ammPools)
      setCurOpen(false)
    } else if (index === 'open') {
      setNowPool([])
      getOpenList()
      setCurOpen(true)
    }
  }


  const getStakedPools = async () => {
    let filterPools = [...poolManager.ammPools, ...poolManager.lpPools];
    let arr = await Promise.all(filterPools.map(async (item) => {
      let params = [item.chainId, item.address, wallet.detail.account]
      let res = await apiProxy.request('getUserStakeInfo', params)
      if (res.isStaked) {
        return item
      } else {
        return false
      }
    }))
    arr = arr.filter(p => p)
    let ammArr = arr.filter(p => p.type !== 'lp')
    let lpArr = arr.filter(p => p.type === 'lp')
    setStakedAmmList(ammArr)
    setStakedLpList(lpArr)
  }

  const changeStaked = (value) => {
    setCheckedStaked(value)
  }

  const changeOption = (value) => {
    setCheckedOption(value)
  }
  const changeFutureMain = (value) => {
    setCheckedFutureMain(value)
  }
  const changeFutureInno = (value) => {
    setCheckedFutureInno(value)
  }
  const changePolygon = (value) => {
    setCheckedPolygon(value)
  }
  const changeBSC = (value) => {
    setCheckedBSC(value)
  }
  const changeArb = (value) => {
    setCheckedArb(value)
  }
  const changeETH = (value) => {
    setCheckedETH(value)
  }

  const showFilter = () => {
    setIsShowFilter(!isShowFilter)
  }

  const clearFilter = () => {
    setCheckedOption(false)
    setCheckedFutureMain(false)
    setCheckedFutureInno(false)
    setCheckedPolygon(false)
    setCheckedBSC(false)
    setCheckedETH(false)
    setCheckedArb(false)
  }

  useEffect(() => {
    if (wallet.isConnected()) {
      getStakedPools()
    }
  }, [wallet.detail, poolManager, curTabPool])


  useEffect(() => {
    let pool = []
    let filterPools = curTabPool === 'amm' ? poolManager.ammPools : poolManager.lpPools;

    if (checkedOption) {
      let arr = filterPools.filter(p => p.type === 'option')
      pool = [...pool, ...arr]
    }
    if (checkedFutureMain) {
      let arr = filterPools.filter(p => (p.version === 'v2' || p.version === 'v3') && p.type !== 'option')
      pool = [...pool, ...arr]
    }
    if (checkedFutureInno) {
      let arr = filterPools.filter(p => (p.version === 'v2_lite' || p.version === 'v3_lite'))
      pool = [...pool, ...arr]
    }
    if (checkedBSC) {
      // pool = []
      if (checkedOption) {
        let arr = filterPools.filter(p => (+p.chainId === 56 || +p.chainId === 97) && p.type === 'option')
        pool = [...pool, ...arr]
      }
      if (checkedFutureMain) {
        let arr = filterPools.filter(p => (+p.chainId === 56 || +p.chainId === 97) && (p.version === 'v2' || p.version === 'v3') && p.type !== 'option')
        pool = [...pool, ...arr]
      }
      if (checkedFutureInno) {
        let arr = filterPools.filter(p => (+p.chainId === 56 || +p.chainId === 97) && (p.version === 'v2_lite' || p.version === 'v3_lite'))
        pool = [...pool, ...arr]
      }
      if (!checkedOption && !checkedFutureMain && !checkedFutureInno) {
        let arr = filterPools.filter(p => +p.chainId === 56 || +p.chainId === 97)
        pool = [...pool, ...arr]
      }
    }
    if (checkedArb) {
      // pool = []
      if (checkedOption) {
        let arr = filterPools.filter(p => (+p.chainId === 42161 || +p.chainId === 421611) && p.type === 'option')
        pool = [...pool, ...arr]
      }
      if (checkedFutureMain) {
        let arr = filterPools.filter(p => (+p.chainId === 42161 || +p.chainId === 421611) && (p.version === 'v3') && p.type !== 'option')
        pool = [...pool, ...arr]
      }
      if (checkedFutureInno) {
        let arr = filterPools.filter(p => (+p.chainId === 42161 || +p.chainId === 421611) && (p.version === 'v2_lite' || p.version === 'v3_lite'))
        pool = [...pool, ...arr]
      }
      if (!checkedOption && !checkedFutureMain && !checkedFutureInno) {
        let arr = filterPools.filter(p => (+p.chainId === 42161 || +p.chainId === 421611))
        pool = [...pool, ...arr]
      }
    }
    if (checkedPolygon) {
      // pool = []
      if (checkedOption) {
        let arr = filterPools.filter(p => +p.chainId === 137 && p.type === 'option')
        pool = [...pool, ...arr]
      }
      if (checkedFutureMain) {
        let arr = filterPools.filter(p => +p.chainId === 137 && (p.version === 'v2' || p.version === 'v3') && p.type !== 'option')
        pool = [...pool, ...arr]
      }
      if (checkedFutureInno) {
        let arr = filterPools.filter(p => +p.chainId === 137 && (p.version === 'v2_lite' || p.version === 'v3_lite'))
        pool = [...pool, ...arr]
      }
      if (!checkedOption && !checkedFutureMain && !checkedFutureInno) {
        let arr = filterPools.filter(p => +p.chainId === 137)
        pool = [...pool, ...arr]
      }
    }
    if (checkedBSC && checkedPolygon && checkedArb) {
      pool = []
      let arr = filterPools.filter(p => +p.chainId === 137 || +p.chainId === 56 || +p.chainId === 42161)
      pool = [...pool, ...arr]
    }
    if (checkedETH) {
      let arr = filterPools.filter(p => +p.chainId === 1)
      pool = [...pool, ...arr]
    }
    if (!checkedOption && !checkedFutureMain && !checkedFutureInno && !checkedBSC && !checkedPolygon && !checkedETH && !checkedArb) {
      pool = filterPools
    }
    setNowPool(pool)
  }, [checkedOption, checkedFutureMain, checkedFutureInno, checkedBSC, checkedPolygon, checkedETH, checkedArb])

  const getOpenList = async () => {
    await poolManager.loadOpen()
    setNowPool(poolManager.openPools)
  }
  useEffect(() => {
    if (curOpen) {
      setPoolTitle(lang['open-pool-title'])
    } else {
      setPoolTitle(lang['deri-pool-title'])
    }
  }, [curOpen])
  useEffect(() => {
    poolManager.load()
  }, [])
  useEffect(() => {
    if (curTabPool === 'amm') {
      if (!curOpen) {
        setNowPool([])
        if (checkedStaked) {
          setNowPool(stakedAmmList)
        } else {
          setNowPool(poolManager.ammPools)
        }
      } else {
        // setNowPool(poolManager.v2LiteOpenList)
      }
    } else if (curTabPool === 'deri') {
      setNowPool([])
      if (checkedStaked) {
        setNowPool(stakedLpList)
      } else {
        setNowPool(poolManager.lpPools)

      }
    }
  }, [curTabPool, checkedStaked])
  useEffect(() => {
    setNowPool(poolManager.ammPools)
  }, [poolManager.pools])
  useEffect(() => {
    if (wallet.isConnected()) {
      let cookie = getCookie('poolClaim')
      if (cookie === 'true') {
        setPoolClaim(false)
      } else if (nowPool) {
        setPoolClaim(true)
      }
    }
  }, [wallet, nowPool, wallet.detail])
  return (
    <div className='pool-box'>
      {/* {poolClaim && <PoolGuide poolClaim={poolClaim} />}  */}
      <AnnouncementLp wallet={wallet} />
      <div className='pool-info'>
        <div className='left'>
          <div className='left-header-info'>
            <div className='pool-title'>
              <div className='pool-title-text'>
                {poolTitle}
              </div>
              <div className='pool-title-select-text'>
                {!curOpen && <>
                  <div className='pool-test-des'>
                    {lang['pool-title-des']}
                  </div>
                </>}
              </div>
              <div className='select-pool-ves'>
                <Dropdown selectList={dropdownList} onChange={selectPoolOnchage} />
              </div>
              <div className='simple-select-ves'><img alt='' src={selectIcon} /></div>
            </div>
          </div>
          <div className='pool-card-claim-bg'>
            {!curOpen && <div className='pool-tab'>
              <div className='pool-tab-info'>
                <div className='tab-mining'>
                  <div className={curTabPool === 'amm' ? "pool-active" : "pool-unactive"} onClick={() => { setCurTabPool('amm') }} >{lang['amm-liquidity-mining']}</div>
                  <div className={curTabPool === 'deri' ? "pool-active" : "pool-unactive"} onClick={() => { setCurTabPool('deri') }} >{lang['deri-liquidity-mining']}</div>
                </div>
                {/* <Tab tabSet={tabSet} width={374} /> */}
                <div className='simple-tab-mining'>
                  <div className={curTabPool === 'amm' ? "pool-active" : "pool-unactive"} onClick={() => { setCurTabPool('amm') }}>{lang['amm']}</div>
                  <div className={curTabPool === 'deri' ? "pool-active" : "pool-unactive"} onClick={() => { setCurTabPool('deri') }}>{lang['type-deri']}</div>
                </div>
                <div className='filter'>
                  <img alt='' src={filterIcon} className='filter-img' onClick={showFilter} />
                  {isShowFilter && <Filter
                    curTabPool={curTabPool}
                    lang={lang}
                    isShowFilter={isShowFilter}
                    checkedStaked={checkedStaked}
                    setCheckedStaked={setCheckedStaked}
                    setIsShowFilter={setIsShowFilter}
                    setCheckedOption={setCheckedOption}
                    setCheckedFutureMain={setCheckedFutureMain}
                    setCheckedFutureInno={setCheckedFutureInno}
                    setCheckedPolygon={setCheckedPolygon}
                    setCheckedBSC={setCheckedBSC}
                    setCheckedArb={setCheckedArb}
                    setCheckedETH={setCheckedETH}
                    clearFilter={clearFilter}
                    checkedPolygon={checkedPolygon}
                    checkedETH={checkedETH}
                    checkedOption={checkedOption}
                    checkedFutureMain={checkedFutureMain}
                    checkedFutureInno={checkedFutureInno}
                    checkedBSC={checkedBSC}
                    checkedArb={checkedArb}
                  />}
                </div>
              </div>
            </div>}
            <div className='pool-card'>
              {nowPool.map((item, index) => <Card wallet={wallet} lang={lang} pool={item} key={index} />)}
            </div>
            {!curOpen && <Claim lang={lang} wallet={wallet} getLang={getLang} />}
            <div className></div>
          </div>
        </div>
        <div className='right'>
          <div className='right-info'>
            <div className='select-pool-ves-staked'>
              <div className='select-pool-ves'>
                <Dropdown selectList={dropdownList} onChange={selectPoolOnchage} />
              </div>
              {!curOpen && <div className='staked-only'>
                <div className='staked-only-text'>
                  {lang['staked-only']}
                </div>
                <div>
                  <Toggle className='l-toggle' checked={checkedStaked} onClick={changeStaked} />
                </div>
              </div>}
            </div>
            {!curOpen && <>
              <div className='select-pool-type-network'>
                {/* <div className='pool-type'>
                  <div className='type-cols'>{lang['type']}</div>
                  <div className='type-cols'>
                    <div className='type-title'>{lang['options']}</div>
                    <div><Checkbox checked={checkedOption} className='l-checkbox' onChange={changeOption} /></div>
                  </div>
                  <div className='type-cols'>
                    <div className='type-title'>{lang['future-main']}</div>
                    <div><Checkbox checked={checkedFutureMain} className='l-checkbox' onChange={changeFutureMain} /></div>
                  </div>
                  <div className='type-cols'>
                    <div className='type-title'>{lang['inno']}</div>
                    <div><Checkbox checked={checkedFutureInno} className='l-checkbox' onChange={changeFutureInno} /></div>
                  </div>
                </div> */}
                <div className='network'>
                  <div className='type-cols'>{lang['network']}</div>
                  <div className='type-cols'>
                    <div className='type-title'> <img alt='' src={BSC} /> BNB Chain</div>
                    <div><Checkbox checked={checkedBSC} className='l-checkbox' onChange={changeBSC} /></div>
                  </div>
                  {curTabPool !== 'deri' && <>
                    <div className='type-cols'>
                      <div className='type-title'> <img alt='' src={ARBITRUM} /> Arbitrum</div>
                      <div><Checkbox checked={checkedArb} className='l-checkbox' onChange={changeArb} /></div>
                    </div>
                    {/* <div className='type-cols'>
                      <div className='type-title'> <img alt='' src={POLYGON} /> Polygon</div>
                      <div><Checkbox checked={checkedPolygon} className='l-checkbox' onChange={changePolygon} /></div>
                    </div> */}

                  </>}

                  {curTabPool === 'deri' && <div className='type-cols'>
                    <div className='type-title'><img alt='' src={ETH} /> Ethereum</div>
                    <div><Checkbox checked={checkedETH} className='l-checkbox' onChange={changeETH} /></div>
                  </div>}
                </div>
              </div>
              <div className='select-footer'>
                <div className='clear-all-filter' onClick={clearFilter}>
                  {lang['clear-all-filter']}
                </div>
                <div className='deri-logo'>
                  <Icon token='pool-deri-logo' width='72' height='84' />
                </div>
              </div>
            </>}
          </div>
        </div>
      </div>
    </div>
  )
}

export default inject('wallet', 'poolManager')(observer(Index))