import React, { useState, useEffect } from 'react'
import Button from '../../components/Button/Button'
import Slider from '../../components/Slider/TimeSlider';
import useConfig from "../../hooks/useConfig";
import { useAlert } from 'react-alert'
import DeriNumberFormat from "../../utils/DeriNumberFormat"
import apiProxy from '../../model/ApiProxy';
import { eqInNumber } from '../../utils/utils';
import UnderlineText from '../../components/UnderlineText/UnderlineText';
import tipDeri from './img/oldDeri.svg'
import './claim.less'
export default function Claim({ lang, getLang, poolShow = true, wallet = {} }) {
  const alert = useAlert();
  const [claimInfo, setClaimInfo] = useState({})
  const [claimed, setClaimed] = useState(false);
  const [firstTime, setFirstTime] = useState('')
  const [isValid, setIsValid] = useState(false)
  const config = useConfig(claimInfo.chainId)
  const loadClaimInfo = async () => {
    if (wallet.isConnected()) {
      const claimInfo = await apiProxy.request('getUserInfoAll', [wallet.detail.account])
      const claimed = (+claimInfo.total).toFixed(2);
      const unclaimed = claimInfo.valid ? (+claimInfo.amount).toFixed(2) : 0;
      const harvestDeriLp = claimInfo.lp;
      const harvestDeriTrade = (+claimInfo.trade).toFixed(2);
      const chainId = claimInfo.chainId
      setClaimInfo({ claimed, unclaimed, harvestDeriLp, harvestDeriTrade, chainId })
    }
  }

  const getUserLastClaimInfo = async () => {
    if (wallet.isConnected()) {
      let res = await apiProxy.request("getUserLastClaimInfo", [wallet.detail.account])
      if (res) {
        setIsValid(!res.claimed)
      }
    }

  }

  const claimDeri = async () => {
    if (config.name === undefined) {
      alert.error(`${lang['wrong-network']}`);
      return;
    }
    if (!eqInNumber(wallet.detail.chainId, claimInfo.chainId)) {
      alert.error(`${lang['your-deri-is-on']} ${config.name} ${lang['connect-to']} ${config.name} ${lang['to-claim']}`)
      return;
    }
    if ((+claimInfo.unclaimed) === 0) {
      alert.error(lang['no-deri-to-claim-yet']);
      return;
    }
    let now = parseInt(Date.now() / 1000) % (3600 * 8);
    if (now < 1800) {
      alert.error(lang['claiming-DERI-is-disabled-during-first-30-minutes-of-each-epoch']);
      return;
    }
    let res = await apiProxy.request('mintDToken', [wallet.detail.chainId, wallet.detail.account], {
      includeResponse: true,
      write: true,
      subject: lang['claim-deri-title'],
      title:{
        processing:lang['claim-deri-processing'],
        success:lang['claim-reward-executed'],
        error:lang['claim-reward-failed'],
      },
      content:{
        success:lang['claim-deri-reward'],
        error:lang['transaction-failed'],
      }
    })

  }
  const click = async () => {
    if (wallet.isConnected()) {
      const res = await claimDeri();
      if (res) {
        setClaimed(true)
      }
    } else {
      try {
        const result = await wallet.connect();
        return result ? true : false
      } catch (e) {
        return false
      }
    }
  }

  const getMonth = (month) => {
    let monthText;
    switch (month) {
      case 1:
        monthText = lang['jan']
        break;
      case 2:
        monthText = lang['feb']
        break;
      case 3:
        monthText = lang['mar']
        break;
      case 4:
        monthText = lang['apr']
        break;
      case 5:
        monthText = lang['may']
        break;
      case 6:
        monthText = lang['jun']
        break;
      case 7:
        monthText = lang['jul']
        break;
      case 8:
        monthText = lang['aug']
        break;
      case 9:
        monthText = lang['sep']
        break;
      case 10:
        monthText = lang['oct']
        break;
      case 11:
        monthText = lang['nov']
        break;
      case 12:
        monthText = lang['dec']
        break;
      default:
        break;
    }
    return monthText
  }

  const getUserFirstClaimInfo = async () => {
    if (wallet.isConnected()) {
      let res = await apiProxy.request('getUserFirstClaimInfo', [wallet.detail.account])
      if (res.first_timestamp) {
        let date = new Date(res.first_timestamp * 1000)
        let year = date.getFullYear()
        let month = date.getMonth() + 1;
        month = getMonth(month)
        let day = date.getDate();
        setFirstTime(`${month} ${day}, ${year}`)
      } else {
        setFirstTime('')
      }
    }
  }
  useEffect(() => {
    let interval = window.setInterval(()=>{loadClaimInfo();getUserLastClaimInfo()}, 1000 * 60 * 3);
    loadClaimInfo();
    // getUserLastClaimInfo()
    // getUserFirstClaimInfo();
    return () => clearInterval(interval);
  }, [wallet.detail.account])
  return (
    <div className='mining-claim'>
      <div className='mining-claim-info'>
        <div className='deri-num-slider'>
          <div className='deri-claim-time'>
            {lang['old-deri-farms']}
            <UnderlineText tip={lang['old-deri-farms-tip']} multiline={true}>
              <span>
                <img src={tipDeri} />
              </span>
            </UnderlineText>
          </div>
          <div className='deri-claim-text'>
            {getLang('old-pool-deri', { num: claimed ? 0 : claimInfo.unclaimed ? !isValid ? 0 : claimInfo.unclaimed : 0 })}
          </div>
        </div>
        {poolShow && <div className='cliam-button'>
          <Button icon='ClaimDeri' fontWeight='bold' disabled={+claimInfo.unclaimed === 0 ? true : !isValid ? true : false} label={lang['claim-all']} onClick={click} />
        </div>}
      </div>
    </div>
  )
}