
import React, { useState, useEffect } from 'react'
import hint from './img/hint.svg'
import skip from './img/skip.svg'
import { bg } from '../../lib/web3js/index';
import { useAlert } from 'react-alert'
import { show } from 'react-functional-modal'
import closeModalIcon from '../../assets/img/close-modal-icon.svg'
import { setCookie, getCookie } from '../../utils/utils'
import apiProxy from '../../model/ApiProxy'
import './hint.less'
import { DeriEnv } from '../../lib/web3js/shared/config/env';
import LiquidityGuidance from "../LiquidityGuidance/LiquidityGuidance"
export default function Announcement({ wallet }) {
  const [isSkip, setIsSkip] = useState(true)
  const [deriNum, setDeriNum] = useState(0)
  const alert = useAlert();
  const closeHint = () => {
    if (isSkip) {
      setCookie('close-lp-hint', "false", 1)
    }
    setDeriNum(0)
  }
  const showLiquidity = () => {
    if (wallet.isConnected()) {
      show(<LiquidityGuidance wallet={wallet} alert={alert} />, {
        key: 'LiquidityGuidance',
        fading: true,
        style: {
          background: "rgba(0, 0, 0, 0.4)",
          zIndex: 2,
        }
      })
    }
  }
  const getinfo = async () => {
    let res = await apiProxy.request("getLiquidityInfo", [wallet.detail.chainId, "0x26bE73Bdf8C113F3630e4B766cfE6F0670Aa09cF", wallet.detail.account, 0])
    console.log("getLiquidityInfo", res)
    const share = res.shares ? bg(res.shares) : bg(0)
    const total = share.isNaN() ? bg(0) : share.multipliedBy(res.shareValue)
    setDeriNum(total.toFixed(4))
  }

  useEffect(() => {
    let cookieWelcome = getCookie('close-lp-hint')
    if (cookieWelcome === "false") {
      setDeriNum(0)
    }
  }, [deriNum])

  useEffect(() => {
    if (wallet.isConnected()) {
      getinfo()
    }
  }, [wallet, wallet.detail, wallet.detail.account])
  return (
    DeriEnv.get() === 'prod' ? <div>
      {deriNum > 0 && <div className='trade-hint  close-position-hint'>
        <div className='hint-img'>
          <img src={hint} alt='' />
        </div>
        <div className='hint-box'>
          <div className='hint-title'>
            <span>
              For Your Information
            </span>
            <img className='close' src={closeModalIcon} alt='X' onClick={closeHint} />
          </div>
          <div className='hint-content'>
            <div className='hint-text vip-text'>
              You have staked balance of {deriNum} DERI in the retired Future Inno (DERI-margined) pool.
            </div>
            <div className='learn-more'>
              <br></br>
              <a onClick={showLiquidity}>Click here to migrate the liquidity to the new Inno Pool (DERI-margined)  to keep earning DERI rewards.</a>
            </div>
            <div className='hint-link vip-skip'>
              <div className={isSkip ? 'skip check-skip' : 'skip'} onClick={() => setIsSkip(!isSkip)}>
                {isSkip && <img src={skip} />}
              </div>  <span>skip notification until tomorrow</span>
            </div>
          </div>
        </div>
        <div className='m-check' onClick={closeHint}>
          <span className='check'>
            OK
          </span>
        </div>
      </div>}

    </div> : null

  )
}